import styled from '@emotion/styled';

interface ButtonProps {
  primary?: 'primary' | 'secondary';
  backgroundColor?: string;
  size?: 'small' | 'medium' | 'large'| 'responsive';
  label: string;
  onClick?: () => void;
  theme?: 'Con' | 'Muji' | 'JayG';
}

const Button = ({
  primary = 'secondary',
  size = 'medium',
  backgroundColor = '',
  label,
  theme,
  ...props
}: ButtonProps) => {

const styleByTheme = {
  'Con': {
    primary: 'button--Con-primary',
    secondary: 'button--Con-secondary',
  },
  'Muji': {
    primary: 'button--Muji-primary',
    secondary: 'button--Muji-secondary',
  },
  'JayG': {
    primary: 'button--JayG-primary',
    secondary: 'button--JayG-secondary',
  },
};

// mode 변수에서 결정한 primary prop의 값에 따라 CSS 적용
const mode = theme ? `button--${theme.toLowerCase()}-${primary ? 'primary' : 'secondary'}` : '';
const sizeClass = size === 'responsive' ? 'button--responsive' : `button--${size}`;
const themeClass = theme? styleByTheme[theme] : '';

  return (
    <StyledButton
      type='button'
      className={['button', sizeClass, mode, themeClass].join(' ')}
      style={{ backgroundColor }}
      {...props}
    >
      {label}
    </StyledButton>
  );
};

const StyledButton = styled.button`
  font-family: 'Nunito Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 700;
  border: 0;
  border-radius: 3em;
  cursor: pointer;
  display: inline-block;
  line-height: 1;

  &.button--small {
    font-size: 12px;
    padding: 10px 16px;
  }

  &.button--medium {
    font-size: 14px;
    padding: 11px 20px;
  }

  &.button--large {
    font-size: 16px;
    padding: 12px 24px;
  }

  @media (max-width: 600px) {
    &.button--responsive {
      font-size: 12px;
      padding: 8px 16px;
    }
  }

  @media (min-width: 601px) and (max-width: 1200px) {
    &.button--responsive {
      font-size: 24px;
      padding: 16px 32px;
    }
  }

  @media (min-width: 1201px) {
    &.button--responsive {
      font-size: 36px;
      padding: 24px 48px;
    }
  }

  &.button--Con-primary {
    background-color: green !important;
    color: white !important;
  }

  &.button--Con-secondary {
    background-color: lightgreen !important;
    color: darkgreen !important;
  }

  &.button--Muji-primary {
    background-color: yellow !important;
    color: black !important;
  }

  &.button--Muji-secondary {
    background-color: lightyellow !important;
    color: darkyellow !important;
  }

  &.button--Jayg-primary {
    background-color: brown !important;
    color: whitesmoke !important;
  }

  &.button--Jayg-secondary {
    background-color: lightbrown !important;
    color: darkbrown !important;
  }
`;

export default Button;